#CalculatorPage .container {
    margin: auto;
    width: 100%;
    align-items: center;
    justify-content: center;
}

/* .container:hover{
    transform: scale(1.05);
} */
#CalculatorPage .calculator {
    margin: auto;
    background: rgb(30, 222, 232);
    padding: 20px;
    border-radius: 10px;
    margin-top: 150px;
    width: 175px;
    box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
}

#CalculatorPage .calculator input {

    margin-top: 7px;
    /* border: 5px; */
    outline: 10px;
    width: 40px;
    height: 40px;
    border-radius: 10px;
    box-shadow: -8px -8px 15px rgba(255, 255, 255, 0.1), 5px 5px 15px rgba(0, 0, 0, 0.2);
}

#CalculatorPage .calculator  input:hover {
    /* background-color: transparent; */
    /* opacity: 0.8; */
    visibility: visible;
}

#CalculatorPage form .display {
    margin: auto;
    width: 10px;

}

#CalculatorPage  .display input {
    text-align: right;
    font-size: 20px;
    box-shadow: none;
    width: 170px;
}

#CalculatorPage form input.equal {
    width: 85px;
}

#CalculatorPage form input.operator {
    color: #33ffd8;

}