#LoginPage .group{
  background:#dfe9f5;
  width: 330px;
  padding: 2rem 1rem;
  margin: 50px auto;
  background-color: #ffff;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 20px 35px rgba(0, 0, 0, 0.1);
}
#LoginPage h1{
  font-size: 2rem;
  color: #07001f;
  margin-bottom: 1.12rem;
}
#LoginPage input{
  width: 82%;
  outline: none;
  border: 1px solid #ffff;
  padding: 12px 20px;
  margin-bottom: 10px;
  border-radius: 20px;
  background: #e4e4e4;
}
#LoginPage button{
  font-size: 1rem;
  margin-top: 1.8rem;
  padding: 10px;
  border-radius: 20px;
  outline: none;
  border: none;
  width: 90%;
  cursor: pointer;
  background: rgb(17, 107, 143);
}
#LoginPage button:hover{
  background: rgb(17, 187, 0.855);
}
#LoginPage input:focus{
  border: 1px solid rgb(192, 192, 192);
}
#LoginPage .terms{
  margin-top: 0.1px;
}
#LoginPage .terms{
  vertical-align: middle;
  cursor: pointer;
}
#LoginPage .terms label{
  font-size: 15px;
}
#LoginPage .terms a{
  color: rgb(17, 107, 143);
  text-decoration: none;
}
#LoginPage .member{
  font-size: 0.8rem;
  margin-top: 1.4rem;
  color: #636363;
}
#LoginPage .member{
  color: rgb(17, 107, 143);
  text-decoration: none;
}
