
#PaginationPage table {
    margin: centre;
    padding: 10px;
    margin-right: 50px;
    margin-bottom: 30px;
    font-size: large;
    font-weight: bold;
    border: 3px solid rgb(28, 23, 23);
    
    
}

#PaginationPage td {
    margin: centre;
    width: auto;
    border: 3px solid rgb(233, 33, 213);
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 2px;
    padding-bottom: 2px;
}

#PaginationPage .currentPage {
    background-color: blueviolet;
    color: whitesmoke;
}

